//Ï§ëÎ≥µÎêú ÏΩîÎìúÎ•º Î¶¨Ìå©ÌÜ†Î¶¨ 

//htmlÏóêÏÑú container ÌÉúÍ∑∏Î•º Í∞ÄÏ†∏Ïò¥
const container = document.getElementById('root');
//ÏÑúÎ≤Ñ ÌÜµÏã†ÏùÑ ÏúÑÌï¥ÏÑú XMLHttpRequest() Î≥ÄÏàò ajax Îã¥ÎäîÎã§ 
const ajax = new XMLHttpRequest();

const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';

//Í≥µÏú†Ìï†ÏàòÏûàÎäî Î≥ÄÏàòÏÑ§Ï†ï
const store = {
    currentPage: 1,
    feeds: [],
}

//Ï§ëÎ≥µÎêòÎäî AJAX Ìï®ÏàòÎ•º ÎßåÎì§Í∏∞ Î∞îÎÄåÎäî Î∂ÄÎ∂ÑÏùÄ URL Î∞ñÏóê ÏóÜÍ∏∞ ÎñÑÎ¨∏Ïóê Ïù∏ÏûêÎ°ú URL ÎÑ£Í≥† Ìò∏Ï∂úÌï†ÎñÑÎßàÎã§ URL Î∞îÍøîÏ§ÄÎã§
function getDate(URL){
    //ajax ÌÜµÏã† open , send , response
    //ajx.open('method Î∞©Ïãù' , 'api url' , ÎπÑÎèôÍ∏∞ÌÜµÏã† ÏïàÌïúÎã§ÎäîÎúªÏûÖÎãàÎã§)
    ajax.open('GET' , URL  , false)
    ajax.send();
    //JOSON ÌòïÌÉúÎ°ú return Í∞íÏùÑ Î∞õÎäîÎã§
    return JSON.parse(ajax.response);
 
};

function makeFeeds(feeds){
    for(let i = 0; i < feeds.length; i++){
      feeds[i].read = false;
    }
    return feeds;
};

//div ÏÉùÏÑ±
const Content = document.createElement('div');

//createElement ÏÉùÏÑ± 
const ul = document.createElement('ul');


function newsFeed(){
    let newsFeed = store.feeds;

    const newsList = [];

    // ÌÖúÌîåÎ†õ ÌÅ∞ÌãÄÏùÑ ÎßåÎì§Ïñ¥ÏÑú UI Î∞∞Ïπò
    let template = `
        <div class="bg-gray-600 min-h-screen">
        <div class="bg-white text-xl">
        <div class="mx-auto px-4">
            <div class="flex justify-between items-center py-6">
            <div class="flex justify-start">
                <h1 class="font-extrabold">Hacker News</h1>
            </div>
            <div class="items-center justify-end">
                <a href="#/page/{{__prev_page__}}" class="text-gray-500">
                Previous
                </a>
                <a href="#/page/{{__next_page__}}" class="text-gray-500 ml-4">
                Next
                </a>
            </div>
            </div> 
        </div>
        </div>
        <div class="p-4 text-2xl text-gray-700">
        {{__news_feed__}}        
        </div>
    </div>
    `;

    if(newsFeed.length === 0){
      newsFeed = store.feeds = makeFeeds(getDate(NEWS_URL))
    }

   

    //forÎ¨∏ÏúºÎ°ú Î®ºÏ†Ä 10Ïùò titleÍ∞íÏùÑ Í∞ÄÏ†∏Ïò§Í∏∞
    //Î¨∏ÏûêÏó¥Î°ú html ÎßåÎì§Í∏∞ ÎêòÎèÑÎ°ù DOMÍ∞ùÏ≤¥Ïóê ElementÏÜçÏÑ±ÏùÑ ÏïàÏì∞Í≥† UIÎ•º Íµ¨ÏÑ±ÌïòÎäîÍ≤ÉÏù¥ Ìö®Ïú®Ï†ÅÏù¥Í≥† Ï¢ãÏùå
    for(let i = (store.currentPage -1) * 10; i < store.currentPage * 10; i++){
        newsList.push(`
            <div class="p-6 ${newsFeed[i].read ? 'bg-red-500' : 'bg-white'} mt-6 rounded-lg shadow-md transition-colors duration-500 hover:bg-green-100">
            <div class="flex">
            <div class="flex-auto">
                <a href="#/show/${newsFeed[i].id}">${newsFeed[i].title}</a>  
            </div>
            <div class="text-center text-sm">
                <div class="w-10 text-white bg-green-300 rounded-lg px-0 py-2">${newsFeed[i].comments_count}</div>
            </div>
            </div>
            <div class="flex mt-3">
            <div class="grid grid-cols-3 text-sm text-gray-500">
                <div><i class="fas fa-user mr-1"></i>${newsFeed[i].user}</div>
                <div><i class="fas fa-heart mr-1"></i>${newsFeed[i].points}</div>
                <div><i class="far fa-clock mr-1"></i>${newsFeed[i].time_ago}</div>
            </div>  
            </div>
        </div>  
        `);
    };

    //ÏÇºÌï©Ïó∞ÏÇ∞ÏûêÎ°ú page 1ÏóêÏÑú 0ÏúºÎ°ú ÏïàÎêòÍ≤å ÌñàÏäµÎãàÎã§


    template = template.replace('{{__news_feed__}}' , newsList.join(''));
    template = template.replace('{{__prev_page__}}' , store.currentPage > 1 ? store.currentPage -1 : 1);
    template = template.replace('{{__next_page__}}' , store.currentPage + 1);

    // join() Î©îÏÑúÎìúÎäî Î∞∞Ïó¥Ïùò Î™®Îì† ÏöîÏÜåÎ•º Ïó∞Í≤∞Ìï¥ ÌïòÎÇòÏùò Î¨∏ÏûêÏó¥Î°ú ÎßåÎì≠ÎãàÎã§.
    container.innerHTML = template
}

function newsDetail(){
    //newsContent API url Î≥¥Î©¥ @id Î∂ÄÎ∂ÑÏóê @ÏßÄÏö∞Í∏∞ ÏúÑÌï¥ substr ÏçªÎã§
    const id = location.hash.substr(7);
    console.log('hash Î≥ÄÍ≤ΩÎê®');

    //hash Í∞íÏùÑ Íµ¨ÌïòÍ∏∞ÏúÑÌï¥ÏÑú replace Î©îÏÜåÎìúÎ•º ÏÜçÏÑ±ÏùÑ Ïç®Í±∞ Ï≤´Î≤àÏ®∞ Ïù∏ÏûêÎ•º ÎëêÎ≤àÏ®∞ Ïù∏ÏûêÎ°ú Î∞îÍæºÎã§.
    const newsContent = getDate(CONTENT_URL.replace('@id', id));
    let template = `
    <div class="bg-gray-600 min-h-screen pb-8">
    <div class="bg-white text-xl">
      <div class="mx-auto px-4">
        <div class="flex justify-between items-center py-6">
          <div class="flex justify-start">
            <h1 class="font-extrabold">Hacker News</h1>
          </div>
          <div class="items-center justify-end">
            <a href="#/page/${store.currentPage}" class="text-gray-500">
              <i class="fa fa-times"></i>
            </a>
          </div>
        </div>
      </div>
    </div>

    <div class="h-full border rounded-xl bg-white m-6 p-4 ">
      <h2>${newsContent.title}</h2>
      <div class="text-gray-400 h-20">
        ${newsContent.content}
      </div>

      {{__comments__}}

    </div>
  </div>
    `;

    for(let i = 0; i < store.feeds.length; i++){
      if(store.feeds[i].id === Number(id)){
        store.feeds[i].read = true;
        break;
      }
    }


    //ÎåìÍ∏Ä Ìï®Ïàò

    function makeComment(comments , called = 0){
        const commentString = [];

        for(let i = 0; i < comments.length; i++){
            commentString.push(`
            <div style="padding-left: ${called * 40}px;" class="mt-4">
            <div class="text-gray-400">
              <i class="fa fa-sort-up mr-2"></i>
              <strong>${comments[i].user}</strong> ${comments[i].time_ago}
            </div>
            <p class="text-gray-700">${comments[i].content}</p>
          </div>  
            `);
            if(comments[i].comments.length > 0){
                commentString.push(makeComment(comments[i].comments , called + 1));
            }
        }


        return commentString.join('');
    }
    //Î¨∏ÏûêÏó¥ ÎßåÎì§Í∏∞
    container.innerHTML = template.replace('{{__comments__}}' , makeComment(newsContent.comments));
}




//ÎùºÏö∞ÌÑ∞ ÏÉùÏÑ± hash ÏóÜÏùÑÎïåÎäî newsFeed() ÏûàÏúºÎ©¥ newDetail();
function router(){
    const routerPath = location.hash;

    if(routerPath === ""){
        newsFeed();
    }else if(routerPath.indexOf('#/page/') >= 0){
        store.currentPage = Number(routerPath.substr(7))
        newsFeed();
    }else{
        newsDetail();
    }
}

//hash event ÏÉùÏÑ±
//hashchange Ïù¥Î≤§Ìä∏Îäî Í∞ôÏùÄ ÌéòÏù¥ÏßÄ ÏïàÏóêÏÑú Ìï¥Ïãú(#)Îßå Î∞îÎÄåÏóàÏùÑ Îïå, Ï¶â ÌéòÏù¥ÏßÄÏóêÏÑú idÍ∞Ä ÏûàÎäî ÏöîÏÜåÎ°ú Ïù¥ÎèôÌïòÍ≤å ÌïòÍ∏∞ÏúÑÌï¥ÏÑú hashÎ•º Ïì¥Îã§ 
window.addEventListener('hashchange' , router);
router();


